	module.exports = {"data":{"package":{"id":"bs-luxon","score":0.5634424433344576,"name":"bs-luxon","version":"0.2.0","description":"Bucklescript bindings for Luxon, written in Reason.","keywords":["date","bucklescript","time","luxon"],"license":"MIT","updated":"2018-01-14T11:39:30.730Z","stars":1,"type":"published","quality":0.7020631657977556,"popularity":0.025352872391938175,"maintenance":0.9827142521655787,"readme":"<h1>Luxon Bindings for ReasonML/Bucklescript</h1>\n<p>This repository contains in-progress Reason/Bucklescript bindings for <a href=\"https://github.com/moment/luxon\">Luxon</a>.</p>\n<h2>Example</h2>\n<p>This is an example from <a href=\"https://github.com/moment/luxon\">Luxon's GitHub page</a> written in Reason using bs-luxon:</p>\n<pre><code class=\"language-javascript\">const { DateTime } = require(\"luxon\");\nDateTime.local().setZone('America/New_York').minus({ weeks: 1 }).endOf('day').toISO()\n</code></pre>\n<pre><code class=\"language-ocaml\">DateTime.(\n  local()\n  |> setZone(\"America/New_York\")\n  |> minus(`DurationObj(makeDurationArgs(~weeks=1, ())))\n  |> endOf(`day)\n  |> toISO()\n)\n</code></pre>\n<p>Look in the <code>example/</code> directory for an example of how to use the bindings. The tests contain a lot more examples.</p>\n<h2>Installation</h2>\n<ol>\n<li>Install the bindings using <code>npm install --save bs-luxon</code></li>\n<li>Add the bindings to <code>bsconfig.json</code>:</li>\n</ol>\n<pre><code class=\"language-json\">{\n  \"bs-dependencies\": [\n      \"bs-luxon\"\n  ]\n}\n</code></pre>\n<h2>Build</h2>\n<pre><code>npm run build\n</code></pre>\n<h2>Build + Watch</h2>\n<pre><code>npm run start\n</code></pre>\n<h2>Current State &#x26; Todo</h2>\n<p>The API is still very complete and the bindings are not very \"OCamly\" yet. Use at your own risk.</p>\n","homepageUrl":"https://github.com/davidgomes/bs-luxon","repositoryUrl":"https://github.com/davidgomes/bs-luxon","npmUrl":"https://www.npmjs.com/package/bs-luxon","issuesUrl":"https://github.com/davidgomes/bs-luxon/issues","slug":"packages/bs-luxon"}},"pathContext":{"id":"bs-luxon"}}

